.processing {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100vw;
    height: 100vh;
    top: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.5);
}

.processing__container {
    color: white;
    font-size: 2rem;
    border: 0.2rem solid white;
}

.processing-active {
    visibility: visible;
}


